@import "src/styles/variables";

.textInput{
  color: $font-color;
  font-size: 20px;
  background-color: $primary-light;
  border: $border-color 1px solid;
  padding: 5px;
  border-radius: 6px;
}
.textArea{
  color: $font-color;
  font-size: 20px;
  background-color: $primary-light;
  border: $border-color 1px solid;
  padding: 5px;
  border-radius: 6px;
  resize: none;
}
