@import "src/styles/variables";

.wrapper{
  margin: 24px 12px;
  border: $border-color 1px solid;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  border-radius: 6px;
  transition: border-bottom-width ease-in-out 200ms ;
}
.headerLine{
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 12px 24px;
  font-size: 24px;
  border-bottom: $border-color 1px solid;
  align-items: center;
  transition: background-color 200ms ease-in-out;
  &:hover{
    cursor: pointer;
    background-color:$primary-light;
  }
}
.show{
  transition: max-height ease-in-out 200ms;
  max-height: 5000px;
  visibility: visible;
}
.hide{
  transition: max-height ease-in-out 200ms;
  max-height: 0;
  visibility: hidden;
}
