@import "src/styles/variables";

.button {
  display: flex;
  min-width: 100px;
  align-items: center;
  justify-content: space-around;
  padding: 16px;
  border: none;
  cursor: pointer;
  color: $font-color;
  border-radius: 6px;
}
.blue {
  background-color: $secondary;
  &:hover{
    background-color: #1b6799;
  }
  &:active{
    background-color: #124a6e;
  }
}
.green {
  background-color: $success-color;
  &:hover{
    background-color: #20835f;
  }
  &:active{
    background-color: #177250;
  }
}
.red {
  background-color: $error-color;
  &:hover{
    background-color: #c4142a;
  }
  &:active{
    background-color: #a10d21;
  }
}
