@import "src/styles/variables";

.popUpWrapper{

  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  position: fixed;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.6);
}
.popUpContainer{
  width: 40%;
  background: $primary;
  border-radius: 6px;
  border: 1px solid $border-color;
}
