@import "src/styles/variables";

.header{
  & > div {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  width: 100%;
  height: 20%;
  padding: 26px;
  border-bottom: $border-color 1px solid;
  font-size: 26px;
  box-shadow: 4px 4px 6px -2px rgba(0,0,0,0.9);
  margin-bottom: 25px;
}
.buttonsWrapper{
  display: flex;
  align-items: center;
  & > button {
    margin-left: 24px;
  }
  & > input {
    display: none;
  }
}
.headerText{
  display: flex;
  align-items: center;
}
.uploadButton{
  position: relative;
}
