@import "src/styles/variables";

.buttons {

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 12px;
  margin-top: 24px ;
  button {
    margin-bottom: 12px;
    min-width: auto;
  }
}
.moveButtons {
  display: flex;
  flex-direction: row;
  align-items: center;
  & > button {
    margin-left: 12px;
  }
}
.deleteButton {
  display: flex;
  flex-direction: column;
  & > button {
  }
}
.wrapper{

  display: flex;
  flex-direction: column;
  & > div {
    &:last-child{
      display: flex;
      flex-direction: column;
      justify-content: center;
      flex-grow: 1;
      & > div {
        margin-top: 0;
      }
    }
  }
}
