@import "src/styles/variables";
.scriptSelector{
  padding-bottom: 16px;
  border-bottom: $border-color 1px solid;
}
.scriptSelectorHeader{
  display: flex;
  justify-content: space-between;
  margin-bottom: 12px;
}
.scriptSelectorProperty{
  cursor: pointer;
  display: flex;
  flex-direction: column;
  margin-bottom: 12px;
  & > span {
    margin: 0 12px;
    &:first-child{
      font-size: 18px;
      font-weight: 700;
    }
    &:last-child{
      font-style: italic;
    }
  }
}
.scriptSelectorScript{
  box-shadow: $box-shadow;
  padding: 16px;
  flex-basis: calc(33% - 6px);
  flex-grow: 0;
  flex-shrink: 0;
  border: $border-color 1px solid;
  margin: 6px;
  border-radius: 6px;
  transition: box-shadow,background-color ease-in-out 200ms;
  background-color: $primary;
  &:hover{
    box-shadow: none;
    background-color: #1e1b1f;
  }
  &:active{
    background-color: #141315;
  }
}
.scriptSelectorButton{
  cursor: pointer;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 128px;
  justify-content: center;
  & > span{
    position: relative;
    top: -10px;
    user-select: none;
  }
  &:hover{
    background-color: rgba(224, 226, 219,0.2);
  }
  &:active{
    background-color: rgba(224, 226, 219,0.35);
  }
}
.scriptSelectorScriptsWrapper{
  justify-content: space-around;
  width: 100%;
  display: flex;
  flex-direction: row;
}
.selectorView{
  & > div {
    transition: all ease-in-out 200ms;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
  }
  position: relative;

  overflow: hidden;
  width: 80%;
}
