@import "src/styles/variables";

.selector {
  margin: 16px 32px;
}
.topBar{
  & > span {
    font-size: 24px;
  }
  margin: 16px 32px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid $border-color;
  padding-bottom: 6px;
}
.container{
  border: 1px solid $border-color;
  border-radius: 6px;
  margin: 16px 32px;
  display: flex;
  flex-direction: column;
}
